export default {
	data() {
		return {
			orderInfo: null
		}
	},
	onLoad: function (option) {
		const {
			id
		} = option
		this.getDetail(id)
	},
	methods: {
		getDetail (id){
			const params = {
				id,
				user_id: getApp().globalData.userId,
			}
			this.$api.orderInfo(params).then(res => {
				this.orderInfo = res.data
			})
		}
	}
}
